// app/search/[slug]/page.tsx
import PageContainer from "@/components/Main/PageContainer";
import { H0 } from "@/components/Typography/Heading.styles";
import PostCard from "@/components/PostCard/PostCard";
import { BlogDetails } from "@/types/blog";
import NotFound from "@/components/Main/NotFound";
import PaginationBar from "@/components/Pagination/PaginationBar"; // nếu PaginationBar cần state thì để nó thành client component
import Loading from "@/components/Main/Loading";

interface SearchPageProps {
  params: { slug: string };
  searchParams: { page?: string }; // lấy query ?page=1 từ URL
}

export default async function SearchPage({
  params,
  searchParams,
}: SearchPageProps) {
  const title = decodeURIComponent(params.slug);
  const page = Number(searchParams.page) || 1;

  // gọi API trực tiếp trên server
  const response = await fetch(
    `http://localhost:1337/api/blogs/by-title/${params.slug}?page=${page}&pageSize=3&populate=*`,
    { cache: "no-store" } // đảm bảo mỗi lần gọi đều SSR (không bị cache)
  );

  if (!response.ok) {
    return (
      <PageContainer>
        <NotFound />
      </PageContainer>
    );
  }

  const data = await response.json();
  const blog: BlogDetails[] = data.data;
  const pageCount: number = data.meta.pagination.pageCount;

  if (blog.length === 0) {
    return (
      <PageContainer>
        <NotFound />
      </PageContainer>
    );
  }

  return (
    <PageContainer>
      <div className="flex-2 flex flex-col items-center gap-[50px]">
        <H0>Kết quả tìm kiếm cho: {title}</H0>
        <div className="flex w-full flex-col gap-[50px]">
          {blog.map((item) => (
            <PostCard post={item} key={item.documentId} />
          ))}
        </div>
        {/* Pagination cần client component để đổi page → chuyển query param */}
        <PaginationBar currentPage={page} totalPages={pageCount} />
      </div>
    </PageContainer>
  );
}
